import { apiCall } from '../../services/api'

export default {
  name: 'ArticlePage',
  layout: 'default',
  data() {
    return { articles: null }
  },
  async mounted() {
    await apiCall('get', 'articles').then((response) => {
      this.articles = response
    })
  },
}
